}

.sidebar {
  margin: 0;
  padding: 0;
  width: 200px;
  background-color: #19bbbb;
  color: white;
  position: fixed;
  height: 100%;
  overflow: auto; 
  font-size: 0.9em;
  box-shadow: 0 8px 8px 4px #ccc;
}

.sidebar a {
  display: block;
  color: white;
  padding: 16px;
  text-decoration: none;
}

.sidebar a.active {
  font-size: 1.6em;
  font-weight: bold;
  color: white;
  padding-top: 25px;
  border-bottom: 1px solid #eee;
  text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9;


}

.sidebar a:hover:not(.active) {
  background-color: #555;
  color: white;
}

.content {
  margin-left: 200px;
  padding: 1px 4%;
}

.pcard {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2);
  margin-bottom:20px;

  padding: 2px;
  padding-bottom: 20px;
  background-color: white;
}

.card-header {
  text-align: center;
  width: 100%;
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 20px;
  color: #eee;

  background: #19bbbb;
  padding: 20px 10px;
}

.container h3 { margin:10;}
.container h2 { margin-bottom:10px;}

/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 500px) {
  .sidebar {
    width: 100%;
    position: relative;
    padding-top: 14px;   
    border-bottom: 2px solid white;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2);
    margin-bottom: 5px;

  }
  .sidebar a {
    padding: 10px 17px;
    float: none;
  }

  .topright{
    display: block;
    position: absolute;
    right: 15px;
    top: 20px;
    border-left: 1px solid #1acaca;
  }

  .sidebar a.active {
    padding-top: 10px;
    background-color: #19bbbb;
    font-size: 1.5em;
    font-weight: bold;
    color: white;
    border-bottom: 1px solid white;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9;

  }

  div.content {
    margin-left: 0px;
    padding: 1px 1px;
  }
  .pcard {
    /* Add shadows to create the "card" effect */
    margin:0px 0px;
    margin-left: 1%;
    margin-right: 1%;

  }
  
}


