/* Style for fancy title bar */
.fancy {
line-height: 0.5;
text-align: center;
}
.fancy span {
display: inline-block;
position: relative;  
}
.fancy span:after {
display: block;
content: "";
position: absolute;
height: 1px;
top: 5px;
border-bottom: 2px dotted rgb(92, 92, 92);
width: 100%;
left: 100%;
margin-left: 10px;
}
.fancy span:before {
display: block;
content: "";
position: absolute;
height: 1px;
top: 5px;
border-bottom: 2px dotted rgb(92, 92, 92);
width: 100%;
left: -100%;
margin-left: -10px;
}






